.Buscador {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

h3 {
  color: #ffffff;
  font-size: 1em;
}

p {
  color: #cccccc;
}

ul.categorias {
  list-style: none;
  text-align: left;
  min-width: 100%;
  margin: 0;
    padding: 0;
}

ul.categorias li {
  margin-bottom: 2px;
}

ul.categorias li {
  font-size: 0.5em;
  background: #222222;
  width: 100%;
  display: block;
  padding: 5px 20px;
  color: #ffffff;
  transition: all 0.5s;
  cursor: pointer;
}

ul.categorias li:hover {
  text-decoration: none;
  background-color: #373c46;
}

ul.documentos {
  list-style: none;
  margin: 0;
  padding: 0;
}

ul.documentos li {
  font-size: 0.5em;
  background: #0a4b6d;
  width: 100%;
  display: block;
  padding: 5px 20px;
  color: #ffffff;
  transition: all 0.5s;
  cursor: pointer;
  margin-bottom: 2px;
}

ul.documentos li:hover {
  text-decoration: none;
  background-color: #373c46;
}

h3.tituloseccion, h3.docseccion {
    margin: 0;
    padding: 0;
    font-size: 0.7em;
    color: #678aa9;
}

h3.docseccion {
  color: #afc9df;
}

.react-pdf__Page__canvas {
  width: 100% !important;
  height: 100% !important;
}

.react-pdf__message.react-pdf__message--error {
  display: none;
}

.entrada {
  background-color: #282c34;
  background:url('../images/bkg.png') center center;
  background-size: cover;
  padding: 30px 0;
  overflow: hidden;
  /*-webkit-mask-image:-webkit-gradient(linear, top, bottom, from(rgba(0,0,0,1)), to(rgba(0,0,0,0)));
  mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));*/
}

